@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Playfair+Display&family=Source+Code+Pro&display=swap');

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  color: white;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333333;

}

li {
  float: left;
}

.right {
  float: right;
  margin-right: 80px ;
}
.first {
  margin-left: 80px;
}

li a {
  display: block;
  color: #ff4d00;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  background-color: #111111;
}

article {
  border: 1px solid #ff9a00;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
   margin: auto;
   margin-top: 1%;
}
.mainFlex {
  width: 300px;
  text-align: left;
  margin-left: 1%;
}

.About {
  text-align: left;
  width: 400px;
  margin: auto;
}
.About p {
  font-size: 1.2rem;
}

@media only screen and (max-width: 900px) {
  article {
    flex-direction: column;
    width: 75%;
    text-align: center;
  }
  .mainFlex {
    margin-left: 3%;
  }
}